import React from 'react'
import { createHashRouter, RouterProvider } from 'react-router-dom'
import Layout from './components/Layout/Layout'
import App from './App'
import ProjectPage from './pages/ProjectPage'
import ServicePage from './pages/ServicePage'
import PDFViewerPage from './pages/PDFViewerPage'
import PDFcv from './pages/CV_page'
import PDFservicios from './pages/Catalogo'
import TramitePage from './components/Services/Tramites/TramitePage'
import TramitesIndex from './components/Services/Tramites/TramitesIndex'
import SatelliteExamples from './components/ImageComparison/SatelliteExamples'

// ScrollToHash modificado para trabajar con HashRouter
import { useEffect } from 'react'
import { useLocation } from 'react-router-dom'

const ScrollToHash = () => {
  const location = useLocation()

  useEffect(() => {
    // Para HashRouter hay dos casos:
    // 1. En la ruta principal (#/): buscar un ancla después de la ruta (#/proyectos)
    // 2. En otras rutas (#/proyectos/proyecto-1): buscar un ancla adicional (#/proyectos/proyecto-1#section)
    
    const path = location.pathname
    const hash = location.hash
    
    // Caso especial para la página principal
    if (path === '/' && hash) {
      // Si estamos en la página principal y hay un hash (como "#projects")
      const id = hash.replace('#', '')
      const el = document.getElementById(id)
      if (el) {
        setTimeout(() => {
          el.scrollIntoView({ behavior: 'smooth' })
        }, 100)
      }
    }
    // Para otras rutas con hash adicional
    else if (hash.includes('#', 1)) {
      const secondHashIndex = hash.indexOf('#', 1)
      const id = hash.substring(secondHashIndex + 1)
      const el = document.getElementById(id)
      if (el) {
        setTimeout(() => {
          el.scrollIntoView({ behavior: 'smooth' })
        }, 100)
      }
    }
    
    // Para debugging
    console.log('Path:', path, 'Hash:', hash)
  }, [location])

  return null
}

// Nuevo componente para hacer scroll al principio en cada navegación
const ScrollToTopNavigation = () => {
  const { pathname, hash } = useLocation()

  useEffect(() => {
    if (!hash || !hash.includes('#', 1)) {
      console.log('🔄 ANTES del scroll:', window.scrollY)
      window.scrollTo(0, 0)
      
      // Verificar si realmente funcionó
      setTimeout(() => {
        console.log('✅ DESPUÉS del scroll:', window.scrollY)
      }, 100)
    }
  }, [pathname, hash])

  return null
}

const router = createHashRouter([
  {
    path: '/',
    element: (
      <Layout>
        <ScrollToHash />
        <ScrollToTopNavigation />
      </Layout>
    ),
    children: [
      { index: true, element: <App /> },
      { path: 'proyectos/:slug', element: <ProjectPage /> },
      { path: 'servicios/:slug', element: <ServicePage /> },  

      { path: 'servicios/tramites', element: <TramitesIndex /> },
      { path: 'servicios/tramites/:homoclave', element: <TramitePage /> },

      // 🆕 Nueva ruta para ejemplos satelitales
      { path: 'ejemplos-satelitales', element: <SatelliteExamples /> },
      { path: 'pdf-viewer', element: <PDFViewerPage /> },
      { path: 'cv-pdf', element: <PDFcv /> },
      { path: 'catalogo-servicios', element: <PDFservicios /> },
      
    ]
  }
])

export default function AppRouter() {
  return <RouterProvider router={router} />
}