import { header } from '../../portfolio'
import { useScrollDirection } from '../Hooks/useScrollDirection' // Ajusta la ruta según tu estructura
import Navbar from '../Navbar/Navbar'
import { useState } from 'react'
import './Header.css'

const Header = () => {
  const { homepage, title } = header
  const scrollDirection = useScrollDirection()
  const [isMenuOpen, setIsMenuOpen] = useState(false)

  const getHeaderClasses = () => {
    let classes = 'header center'
    
    if (isMenuOpen) {
      classes += ' header--menu-open'
    } else if (window.innerWidth <= 600) {
      if (scrollDirection === 'down') {
        classes += ' header--hidden'
      } else if (scrollDirection === 'up') {
        classes += ' header--visible'
      }
    }
    
    return classes
  }

  return (
    <header className={getHeaderClasses()}>
      <h3>
        {homepage ? (
          <a href={homepage} className='link'>
            {title}
          </a>
        ) : (
          title
        )}
      </h3>
      <Navbar onMenuToggle={setIsMenuOpen} />
    </header>
  )
}

export default Header