import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Badge} from 'react-bootstrap';
import { useParams, Link } from 'react-router-dom';
import { FiArrowLeft, FiClock, FiDollarSign, FiFileText, FiEye } from 'react-icons/fi';
import Mermaid from '../../Mermaid/Mermaid';
import './TramitePage.css';

// Componente para requisito individual con acordeón
const RequisitoItem = ({ requisito }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  return (
    <div className="requisito-item">
      <div 
        className="requisito-header"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <span className="requisito-number">{requisito.numero}</span>
        <span className="requisito-title">{requisito.titulo}</span>
        <span className="expand-icon">
          {isExpanded ? '−' : '+'}
        </span>
      </div>
      {isExpanded && requisito.descripcion && (
        <div className="requisito-description">
          {requisito.descripcion}
        </div>
      )}
    </div>
  );
};



const TramitePage = () => {
  const { homoclave } = useParams();
  const [tramite, setTramite] = useState(null);
  const [tramitesData, setTramitesData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [diagramMode, setDiagramMode] = useState(null); // 'presencial' o 'en_linea'

  useEffect(() => {
    console.log('=== TRAMITEPAGE DEBUG ===');
    console.log('Homoclave desde URL:', homoclave);
    
    import('../../../data/tramites.json')
      .then(data => {
        console.log('JSON cargado exitosamente:', data.default.length, 'trámites');
        setTramitesData(data.default);
        
        // Buscar por homoclave
        const found = data.default.find(t => t.homoclave === homoclave);
        console.log('Trámite encontrado por homoclave:', found ? 'SÍ' : 'NO');
        
        if (found) {
          console.log('Datos del trámite:', found);
          setTramite(found);
        } else {
          console.log('Trámite NO encontrado. Homoclaves disponibles:', 
            data.default.map(t => t.homoclave).slice(0, 10));
          setError(`Trámite con homoclave "${homoclave}" no encontrado`);
        }
        
        setLoading(false);
      })
      .catch(error => {
        console.error('Error cargando JSON:', error);
        setError('Error cargando datos: ' + error.message);
        setLoading(false);
      });
  }, [homoclave]);

  const generateDiagramFromSteps = (steps, type) => {
    if (!steps || steps.length === 0) {
      return null;
    }

    // Crear diagrama basado en los pasos reales
    let diagram = `graph TD\n    START[Inicio del Trámite]\n`;
    
    steps.forEach((step, index) => {
      const stepId = `STEP${index + 1}`;
      const nextStepId = index + 1 < steps.length ? `STEP${index + 2}` : 'END';
      
      // Truncar descripción para el diagrama
      const shortDesc = step.descripcion.length > 40 
        ? step.descripcion.substring(0, 40) + '...' 
        : step.descripcion;
      
      diagram += `    ${index === 0 ? 'START' : `STEP${index}`} --> ${stepId}["${step.numero}. ${shortDesc}"]\n`;
      
      if (index === steps.length - 1) {
        diagram += `    ${stepId} --> END[Trámite Completado]\n`;
      }
    });

    return diagram;
  };

  const hasValidSteps = (steps) => {
    return steps && Array.isArray(steps) && steps.length > 1;
  };

  if (loading) {
    return (
      <Container className="tramite-page">
        <div className="loading-container">
          <p>Cargando trámite...</p>
          <p>Homoclave: {homoclave}</p>
        </div>
      </Container>
    );
  }

  if (error) {
    return (
      <Container className="tramite-page">
        <Row className="mb-4">
          <Col>
            <Link to="/servicios/tramites" className="btn--back">
              <FiArrowLeft /> regresar al índice
            </Link>
          </Col>
        </Row>
        <Row>
          <Col>
            <div className="error-container">
              <h1>{error}</h1>
              <p>Homoclave buscada: <strong>{homoclave}</strong></p>
              <p>Total de trámites disponibles: {tramitesData.length}</p>
              
              {tramitesData.length > 0 && (
                <div className="available-tramites">
                  <h3>Homoclaves disponibles (primeros 10):</h3>
                  <ul>
                    {tramitesData.slice(0, 10).map(t => (
                      <li key={t.homoclave}>
                        <Link to={`/servicios/tramites/${t.homoclave}`}>
                          {t.homoclave}
                        </Link>
                        <br />
                        <small>{t.nombre}</small>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </Col>
        </Row>
      </Container>
    );
  }

  if (!tramite) {
    return (
      <Container className="tramite-page">
        <h1>Trámite no encontrado</h1>
        <Link to="/servicios/tramite" className="btn btn--outline">Volver al índice</Link>
      </Container>
    );
  }

  return (
    <Container fluid className="tramite-page">
      <Row className="mb-4">
        <Col>
          <Link to="/servicios/tramites" className="btn--back">
            <FiArrowLeft /> regresar al índice
          </Link>
        </Col>
      </Row>

      <Row>
        <Col>

            {/* Header del trámite - VERSIÓN CORREGIDA */}
            <div className="tramite-header">
            <h1 className="tramite-header__title">{tramite.nombre}</h1>
            <div className="tramite-header__meta">
                <div className="meta-line">
                <span><strong>Dependencia:</strong> {tramite.dependencia}</span>
                <span><strong>Homoclave:</strong> {tramite.homoclave}</span>
                </div>
                {tramite.unidad_administrativa && (
                <div className="meta-line">
                    <span><strong>Unidad Administrativa:</strong> {tramite.unidad_administrativa}</span>
                </div>
                )}
            </div>
            </div>

          {
          /* Header - versión 2 
          <div className="tramite-header">
            <div className="tramite-header__badges">
              <Badge bg="info">{tramite.modalidad ? tramite.modalidad.replace('_', ' ') : 'No especificado'}</Badge>
              <Badge bg={tramite.complejidad === 'alta' ? 'danger' : tramite.complejidad === 'media' ? 'warning' : 'success'}>
                {tramite.complejidad || 'media'}
              </Badge>
            </div>
            <h1 className="tramite-header__title">{tramite.nombre}</h1>
            <div className="tramite-header__meta">
              <span><strong>Dependencia:</strong> {tramite.dependencia}</span>
              {tramite.homoclave && <span><strong>Homoclave:</strong> {tramite.homoclave}</span>}
            </div>
          </div>*/
          }

          {/* Métricas principales */}
          <div className="tramite-metrics">
            <div className="metric-card">
              <FiClock className="metric-icon" />
              <div className="metric-content">
                <div className="metric-label">Tiempo de respuesta</div>
                <div className="metric-value">{tramite.tiempo_respuesta}</div>
              </div>
            </div>
            <div className="metric-card">
              <FiDollarSign className="metric-icon" />
              <div className="metric-content">
                <div className="metric-label">Costo del trámite</div>
                <div className="metric-value">{tramite.costo}</div>
              </div>
            </div>
            <div className="metric-card">
              <FiFileText className="metric-icon" />
              <div className="metric-content">
                <div className="metric-label">Requisitos</div>
                <div className="metric-value">
                  {tramite.requisitos_detallados ? tramite.requisitos_detallados.length : 0} documentos
                </div>
              </div>
            </div>
          </div>

          {/* Descripción */}
          {tramite.descripcion && (
            <div className="tramite-section">
              <h3>Descripción del Trámite</h3>
              <p>{tramite.descripcion}</p>
            </div>
          )}

{/* Diagrama de proceso - VERSIÓN CORREGIDA */}
{(hasValidSteps(tramite.tramite_presencial) || hasValidSteps(tramite.tramite_en_linea)) && (
  <div className="tramite-section">
    <h3>Proceso del Trámite</h3>
    
    {/* Botones para mostrar diagramas */}
    <div className="diagram-controls">
      {hasValidSteps(tramite.tramite_presencial) && (
        <button 
          className={`diagram-btn ${diagramMode === 'presencial' ? 'active' : ''}`}
          onClick={() => setDiagramMode(diagramMode === 'presencial' ? null : 'presencial')}
        >
          <FiEye className="me-2" />
          Trámite Presencial
        </button>
      )}
      {hasValidSteps(tramite.tramite_en_linea) && (
        <button 
          className={`diagram-btn ${diagramMode === 'en_linea' ? 'active' : ''}`}
          onClick={() => setDiagramMode(diagramMode === 'en_linea' ? null : 'en_linea')}
        >
          <FiEye className="me-2" />
          Trámite en Línea
        </button>
      )}
    </div>

    {/* Mostrar diagrama solo si hay modo seleccionado */}
    {diagramMode === 'presencial' && (
      <div className="diagram-container">
        <Mermaid steps={tramite.tramite_presencial} />
      </div>
    )}

    {diagramMode === 'en_linea' && (
      <div className="diagram-container">
        <Mermaid steps={tramite.tramite_en_linea} />
      </div>
    )}
  </div>
)}

{/* Requisitos detallados - VERSIÓN ACORDEÓN */}
{tramite.requisitos_detallados && tramite.requisitos_detallados.length > 0 && (
  <div className="tramite-section">
    <h3>Requisitos y Documentación</h3>
    <div className="requisitos-list">
      {tramite.requisitos_detallados.map((req, idx) => (
        <RequisitoItem key={idx} requisito={req} />
      ))}
    </div>
  </div>
)}

          {/* Información adicional */}
          {(tramite.vigencia || tramite.mas_informacion) && (
            <div className="tramite-section">
              <h3>Información Adicional</h3>
              {tramite.vigencia && (
                <div className="info-item">
                  <strong>Vigencia:</strong> {tramite.vigencia}
                </div>
              )}
              {tramite.mas_informacion && (
                <div className="info-item">
                  <strong>Más información:</strong> {tramite.mas_informacion}
                </div>
              )}
            </div>
          )}

          {/* Consideraciones importantes */}
          <div className="tramite-section important-considerations">
            <h3>Consideraciones Importantes</h3>
            <div className="considerations-grid">
              <div className="consideration-card">
                <h4>Tiempo Real vs. Oficial</h4>
                <p>
                  <strong>Oficial:</strong> {tramite.tiempo_respuesta}<br/>
                  <strong>En la práctica:</strong> Considere tiempo adicional para preparación de documentos y posibles observaciones.
                </p>
              </div>
              <div className="consideration-card">
                <h4>Puntos Críticos</h4>
                <ul>
                  <li>Verificar vigencia de todos los documentos</li>
                  <li>Revisar requisitos actualizados antes de iniciar</li>
                  {tramite.tramite_presencial && tramite.tramite_presencial.length > 0 && (
                    <li>Para trámite presencial: agendar cita con anticipación</li>
                  )}
                </ul>
              </div>
            </div>
          </div>

          {/* Consideraciones importantes Original */}
            <div className="tramite-section consultant-insights">
            <h3> Consideraciones Importantes</h3>
            <div className="insight-grid">
              <div className="insight-card">
                <h4>⏱️ Tiempo Real</h4>
                <p>En la práctica: {tramite.complejidad === 'alta' ? '3-6 meses' : tramite.complejidad === 'media' ? '2-4 meses' : '1-2 meses'}</p>
              </div>
              <div className="insight-card">
                <h4>⚠️ Puntos Críticos</h4>
                <p>{tramite.complejidad === 'alta' ? 'Requiere experiencia técnica especializada' : 'Verificar vigencia de los documentos'}</p>
              </div>
            </div>
          </div>


          {/* Enlaces y acciones */}
          <div className="tramite-links">
            {tramite.url_original && (
              <a 
                href={tramite.url_original} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="btn btn--outline"
              >
                ficha oficial
              </a>
            )}
            {tramite.url_formatos && (
              <a 
                href={tramite.url_formatos} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="btn btn--outline"
              >
                Descargar formatos
              </a>
            )}
            <Link to="/#contact" className="btn btn--primary">
              Solicitar consultoría
            </Link>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default TramitePage;