// SatelliteExamples.js - Ejemplos de uso del comparador

import React, { useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ImageComparison from './ImageComparison';
import './SatelliteExamples.css';

const SatelliteExamples = () => {

    useEffect(() => {
    setTimeout(() => window.scrollTo(0, 0), 50);
    }, []); // Array vacío porque no hay slug en esta página

  const examples = [
    {
      id: 'deforestation-michoacan',
      title: 'Deforestación en Michoacán (2019-2024)',
      beforeImage: '/images/satelital/michoacan-2019.jpg',
      afterImage: 'images/satelital/michoacan-2024.jpg',
      beforeLabel: '2019 - Bosque Intacto',
      afterLabel: '2024 - Deforestación',
      metrics: {
        areaLost: '2,847 hectáreas',
        annualRate: '-1.2% cobertura forestal',
        accuracy: '94.3% precisión'
      },
      technical: {
        satellites: 'Landsat 8/9, Sentinel-2',
        resolution: '10-30 metros',
        period: '5 años de monitoreo'
      }
    },
    {
      id: 'urban-growth-gdl',
      title: 'Crecimiento Urbano - Zona Metropolitana de Guadalajara',
      beforeImage: 'images/satelital/before_example.jpg',
      afterImage: '/images/satelital/after_example.jpg',
      beforeLabel: '2015 - Área Original',
      afterLabel: '2024 - Expansión +35%',
      metrics: {
        growth: '+35% superficie urbana',
        population: '+640,000 habitantes',
        density: 'Reducción del 12%'
      },
      technical: {
        satellites: 'Landsat 7/8/9, Sentinel-2',
        resolution: '10-30 metros',
        period: '9 años análisis'
      }
    },
    {
      id: 'ndvi-analysis',
      title: 'Análisis NDVI - Estrés Hídrico Bajío',
      beforeImage: 'images/satelital/before_example.jpg',
      afterImage: '/images/satelital/after_example.jpg',
      beforeLabel: 'RGB - Color Natural',
      afterLabel: 'NDVI - Índice Vegetación',
      metrics: {
        ndviAvg: 'NDVI promedio: 0.67',
        stressAreas: '15% áreas de estrés',
        correlation: 'R² = 0.89 con precipitación'
      },
      technical: {
        satellites: 'MODIS, Landsat 8/9',
        resolution: '250m - 30m',
        period: 'Serie temporal 6 años'
      }
    },
    {
      id: 'water-quality',
      title: 'Calidad de Agua - Laguna de Chapala',
      beforeImage: 'images/satelital/before_example.jpg',
      afterImage: '/images/satelital/after_example.jpg',
      beforeLabel: 'Condiciones Normales',
      afterLabel: 'Eutrofización Detectada',
      metrics: {
        chlorophyll: 'Clorofila-a: 24.5 μg/L',
        turbidity: 'Turbidez aumentó 45%',
        area: '1,112 km² monitoreados'
      },
      technical: {
        satellites: 'Sentinel-2, Landsat 8/9',
        resolution: '10-30 metros',
        period: 'Monitoreo continuo 4 años'
      }
    }
  ];

  return (
    <Container className="satellite-examples">
      <div className="examples-header text-center mb-5">
        <h2 className="section__title">Análisis de Imágenes Satelitales</h2>
        <p className="lead">
          Casos de estudio utilizando comparación interactiva de imágenes
        </p>
      </div>

      {examples.map((example, index) => (
        <div key={example.id} className="example-section mb-5">
          <Row>
            <Col lg={8}>
              <ImageComparison
                beforeImage={example.beforeImage}
                afterImage={example.afterImage}
                beforeLabel={example.beforeLabel}
                afterLabel={example.afterLabel}
                title={example.title}
                className="mb-4"
              />
            </Col>
            <Col lg={4}>
              <div className="example-info">
                <div className="info-card mb-3">
                  <h5>📊 Resultados</h5>
                  {Object.entries(example.metrics).map(([key, value]) => (
                    <p key={key} className="mb-1">
                      <strong>{value}</strong>
                    </p>
                  ))}
                </div>
                
                <div className="info-card">
                  <h5>🛰️ Datos Técnicos</h5>
                  {Object.entries(example.technical).map(([key, value]) => (
                    <p key={key} className="mb-1">
                      <strong>{key === 'satellites' ? 'Satélites' : 
                               key === 'resolution' ? 'Resolución' : 'Período'}:</strong> {value}
                    </p>
                  ))}
                </div>
              </div>
            </Col>
          </Row>
          
          {index < examples.length - 1 && <hr className="example-divider" />}
        </div>
      ))}

      {/* Sección de tecnologías */}
      <div className="tech-section mt-5 p-4">
        <Row>
          <Col md={8}>
            <h4>Stack Tecnológico Utilizado</h4>
            <p>
              Implementación del comparador usando React y integración con 
              herramientas de análisis geoespacial profesional.
            </p>
          </Col>
          <Col md={4}>
            <div className="tech-stack">
              <span className="tech-badge">React</span>
              <span className="tech-badge">Google Earth Engine</span>
              <span className="tech-badge">Python/GeoPandas</span>
              <span className="tech-badge">QGIS</span>
              <span className="tech-badge">Sentinel-2</span>
              <span className="tech-badge">Landsat</span>
            </div>
          </Col>
        </Row>
      </div>
    </Container>
  );
};

export default SatelliteExamples;