import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Accordion } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FiArrowLeft } from 'react-icons/fi';
import './TramitesIndex.css';

const TramitesIndex = () => {
  const [tramitesData, setTramitesData] = useState([]);
  const [tramitesPorCategoria, setTramitesPorCategoria] = useState({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    import('../../../data/tramites.json')
      .then(data => {
        console.log('Trámites cargados:', data.default.length);
        setTramitesData(data.default);
        
        // Organizar por categoría
        const porCategoria = {};
        data.default.forEach(tramite => {
          const cat = tramite.categoria || 'general';
          if (!porCategoria[cat]) {
            porCategoria[cat] = [];
          }
          porCategoria[cat].push(tramite);
        });
        
        console.log('Categorías organizadas:', Object.keys(porCategoria));
        setTramitesPorCategoria(porCategoria);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error cargando trámites:', error);
        setLoading(false);
      });
  }, []);

  const getCategoryTitle = (categoria) => {
    const titles = {
      'evaluacion_impacto': 'Evaluación de Impacto Ambiental',
      'residuos': 'Gestión de Residuos',
      'forestal': 'Recursos Forestales',
      'agua': 'Recursos Hídricos',
      'aire': 'Calidad del Aire',
      'biodiversidad': 'Biodiversidad y Vida Silvestre',
      'verificacion': 'Verificación e Inspección',
      'autorizaciones': 'Autorizaciones y Licencias',
      'registro': 'Registros',
      'general': 'Otros Trámites'
    };
    return titles[categoria] || categoria.replace('_', ' ').toUpperCase();
  };

  if (loading) {
    return (
      <Container className="tramites-index">
        <div className="loading-container">
          <p>Cargando trámites...</p>
        </div>
      </Container>
    );
  }

  return (
    <Container fluid className="tramites-index">
      <Row className="mb-4">
        <Col>
          <Link to="../../#servicios" className="btn--back">
            <FiArrowLeft /> regresar a servicios
          </Link>
        </Col>
      </Row>

      <Row className="mb-4">
        <Col>
          <div className="page-header">
            <h1 className="page-title">Gestión de Trámites Ambientales</h1>
            <p className="page-subtitle">
              {tramitesData.length} trámites organizados por categoría
            </p>
          </div>
        </Col>
      </Row>

      <Row>
        <Col>
          <Accordion defaultActiveKey="CLOSED" className="tramites-accordion"> {/* Cambiara a defaultActiveKey ="0" para mostrar el primer elemento desplegado */}
            {Object.keys(tramitesPorCategoria).map((categoria, index) => (
              <Accordion.Item eventKey={index.toString()} key={categoria}>
                <Accordion.Header>
                  <div className="accordion-header-content">
                    <span className="category-title">{getCategoryTitle(categoria)}</span>
                    <span className="category-count">
                      {tramitesPorCategoria[categoria].length}
                    </span>
                  </div>
                </Accordion.Header>
                <Accordion.Body>
                  <div className="tramites-list">
                    {tramitesPorCategoria[categoria].map(tramite => (
                      <div key={tramite.homoclave} className="tramite-item">
                        <div className="tramite-content">
                          <h4 className="tramite-name">{tramite.nombre}</h4>
                          <div className="tramite-meta">
                            <span className="tramite-dependencia">{tramite.dependencia}</span>
                            {tramite.unidad_administrativa && (
                              <span className="tramite-unidad">{tramite.unidad_administrativa}</span>
                            )}
                          </div>
                        </div>
                        <Link
                          to={`/servicios/tramites/${tramite.homoclave}`}
                          className="tramite-link"
                        >
                          Ver detalles
                        </Link>
                      </div>
                    ))}
                  </div>
                </Accordion.Body>
              </Accordion.Item>
            ))}
          </Accordion>

          {Object.keys(tramitesPorCategoria).length === 0 && (
            <div className="no-results">
              <h3>No se encontraron trámites</h3>
              <p>No hay trámites disponibles en este momento.</p>
            </div>
          )}
        </Col>
      </Row>
    </Container>
  );
};

export default TramitesIndex;