import React, { useState } from 'react';
import './Skills.css';

// Datos simulados (en tu caso vendrían de src/data/skills.json)
const skillsData = {
  "skills": [
    {
      "name": "R",
      "category": "programming",
      "libraries": ["ggplot2", "sf", "raster", "leaflet", "dplyr"],
      "description": "Análisis estadístico y visualización geoespacial"
    },
    {
      "name": "QGis",
      "category": "gis",
      "libraries": ["PyQGIS", "GDAL", "PostGIS", "GRASS"],
      "description": "Sistema de información geográfica open source"
    },
    {
      "name": "ArcGis",
      "category": "gis", 
      "libraries": ["ArcPy", "Model Builder", "Spatial Analyst", "3D Analyst"],
      "description": "Plataforma GIS empresarial de ESRI"
    },
    {
      "name": "Python",
      "category": "programming",
      "libraries": ["geopandas", "rasterio", "folium", "matplotlib", "numpy"],
      "description": "Procesamiento geoespacial y automatización"
    },
    {
      "name": "Earth Engine",
      "category": "cloud",
      "libraries": ["JavaScript API", "Python API", "Cloud Computing"],
      "description": "Análisis satelital a escala planetaria"
    },
    {
      "name": "Imágenes Satelitales",
      "category": "remote-sensing",
      "libraries": ["Sentinel-2", "Landsat", "MODIS", "Planet"],
      "description": "Teledetección y análisis multitemporal"
    },
    {
      "name": "Git",
      "category": "tools",
      "libraries": ["GitHub", "GitLab", "Version Control"],
      "description": "Control de versiones y colaboración"
    },
    {
      "name": "Cartografía",
      "category": "design",
      "libraries": ["Simbología", "Layout", "Web Maps", "Atlas"],
      "description": "Diseño y comunicación cartográfica"
    }
  ]
};

const Skills = () => {
  const [hoveredSkill, setHoveredSkill] = useState(null);

  return (
    <section className='section skills' id='skills'>
      <h2 className='section__title'>Habilidades</h2>
      
      <ul className='skills__list'>
        {skillsData.skills.map((skill, index) => (
          <li 
            key={index}
            className='skills__list-item skill-container'
          >
            <span
              className='skill-button'
              onMouseEnter={() => setHoveredSkill(index)}
              onMouseLeave={() => setHoveredSkill(null)}
            >
              {skill.name}
            </span>
            
            {hoveredSkill === index && (
              <div className='skill-tooltip'>
                <div className='tooltip-content'>
                  {/*<h4>{skill.name}</h4>*/}
                  <p className='skill-description'>{skill.description}</p>
                  <div className='skill-libraries'>
                    {skill.libraries.map((lib, i) => (
                      <span key={i} className='library-tag'>{lib}</span>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </li>
        ))}
      </ul>
    </section>
  );
};

export default Skills;