import React from 'react';
import './Mermaid.css';

const Mermaid = ({ chart, steps, className = '' }) => {
  // Si recibe steps directamente, generar diagrama
  const generateStepsFlow = (stepsArray) => {
    if (!stepsArray || !Array.isArray(stepsArray) || stepsArray.length === 0) {
      return null;
    }

    return (
      <div className="steps-flow">
        <div className="step-item start-step">
          {/*<div className="step-number">📨</div> */}
          <div className="step-content">Inicio del Trámite</div>
        </div>
        
        {stepsArray.map((step, index) => (
          <React.Fragment key={step.numero}>
            <div className="flow-arrow">↓</div>
            <div className="step-item">
              <div className="step-number">{step.numero}</div>
              <div className="step-content">{step.descripcion}</div>
            </div>
          </React.Fragment>
        ))}
        
        <div className="flow-arrow">↓</div>
        <div className="step-item end-step">
          <div className="step-number">✅</div>
          <div className="step-content">Trámite Completado</div>
        </div>
      </div>
    );
  };

  // Si recibe steps, usar flujo de pasos
  if (steps && Array.isArray(steps) && steps.length > 0) {
    return (
      <div className={`mermaid-container ${className}`}>
        {generateStepsFlow(steps)}
      </div>
    );
  }

  // Si recibe chart como string (fallback para otros usos)
  if (!chart) {
    return (
      <div className={`mermaid-container ${className}`}>
        <div className="no-diagram">
          <p>No hay proceso disponible</p>
        </div>
      </div>
    );
  }

  // Para compatibilidad con versiones anteriores
  return (
    <div className={`mermaid-container ${className}`}>
      <div className="legacy-chart">
        <p>Diagrama no disponible</p>
      </div>
    </div>
  );
};

export default Mermaid;