import uniqid from 'uniqid'
import {FiEye, FiMoreHorizontal, FiMoreVertical} from 'react-icons/fi'
import { servicios } from '../../portfolio'
import './Services.css'


// Mapeo de imágenes por servicio
const serviceImages = {
  'cartografia-tematica': '/images/servicios/servicio-cartografia.jpg',
  'analisis-satelital': '/images/servicios/servicio_satelital2.jpg', /*'/images/servicio-tramites.jpg',*/
  'tramites-ambientales': '/images/servicios/servicio-tramites.jpg', /*'/images/servicio-tramites.jpg',*/
  'evaluacion-impacto': '/images/servicios/servicio_eia.jpg',
  'sistemas-gestion': '/images/servicios/servicio-gestion.jpg',
  'analisis-geoespacial': 'images/servicios/servicio_analisis_geoespacial.jpg'
}

const Services = () => {
  if (!servicios.length) return null

  return (
    <section id='servicios' className='section servicios'>
      <h2 className='section__title'>Servicios</h2>

      <div className='servicios__grid'>
        {servicios.map((service) => (
          <ServiceCard 
            key={uniqid()} 
            service={service} 
            image={serviceImages[service.slug]}
          />
        ))}
      </div>
    </section>
  )
}

const ServiceCard = ({ service, image }) => {
  return (
    <div className='service-card'>
      {/* Estado por defecto: Imagen + Título */}
      <div className='service-card__default'>
        <div 
          className='service-card__image'
          style={{ 
            backgroundImage: image 
              ? `url(${image})` 
              : 'linear-gradient(135deg, var(--clr-primary), rgba(41, 120, 181, 0.8))'
          }}
        >
          {/* Ícono fijo arriba a la derecha */}
          <FiMoreVertical className="service-card__icon" />
        </div>

        <div className='service-card__header'>
          <h3 className='service-card__title'>{service.name}</h3>
        </div>
      </div>

      {/* Estado hover/activo: Texto completo */}
      <div className='service-card__details'>
        <div className='service-card__content'>
          <h3 className='service-card__title'>{service.name}</h3>
          
          <p className='service-card__description'>{service.description}</p>

          {service.stack && service.stack.length > 0 && (
            <div className='service-card__stack'>
              {service.stack
                .filter(item => item && item.trim() !== '')
                .slice(0, 4)
                .map((item) => (
                  <span key={uniqid()} className='service-card__tech'>
                    {item}
                  </span>
                ))}
            </div>
          )}

          <div className='service-card__actions'>
            {service.livePreview && (
              <a
                href={service.livePreview}
                className='btn btn--outline'
              >
                Ver Detalles
              </a>
            )}
            
            {/* BOTÓN ESPECIAL PARA TRÁMITES */}
            {service.slug === 'tramites-ambientales' && (
              <a
                href="#/servicios/tramites"
                className='btn btn--outline'
              >
                Buscar por trámite
              </a>
            )}
            
            {service.sourceCode && (
              <a
                href={service.sourceCode}
                target='_blank'
                rel='noreferrer'
                className='btn btn--plain'
              >
                Código
              </a>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Services