import React, { useState } from 'react';
import './Contacto.css';

const Contacto = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });
  
  const [formStatus, setFormStatus] = useState({
    isSubmitting: false,
    message: '',
    type: '' // 'success' or 'error'
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setFormStatus({ isSubmitting: true, message: '', type: '' });

    try {
      // Crear FormData con los datos del formulario
      const formDataToSend = new FormData();
      formDataToSend.append('name', formData.name);
      formDataToSend.append('email', formData.email);
      formDataToSend.append('subject', formData.subject);
      formDataToSend.append('message', formData.message);

      // Enviar a Formspree
      const response = await fetch('https://formspree.io/f/manozedz', {
        method: 'POST',
        body: formDataToSend,
        headers: {
          'Accept': 'application/json'
        }
      });

      if (response.ok) {
        setFormStatus({
          isSubmitting: false,
          message: '¡Mensaje enviado correctamente! Te responderé pronto.',
          type: 'success'
        });
        
        // Limpiar formulario
        setFormData({
          name: '',
          email: '',
          subject: '',
          message: ''
        });
      } else {
        throw new Error('Error en el envío');
      }
      
    } catch (error) {
      setFormStatus({
        isSubmitting: false,
        message: 'Error al enviar el mensaje. Por favor, intenta nuevamente.',
        type: 'error'
      });
    }
  };

  const contactInfo = [
    {
      icon: '📧',
      label: 'Email',
      value: 'tu.email@ejemplo.com',
      link: 'mailto:tu.email@ejemplo.com'
    },
    {
      icon: '📱',
      label: 'Teléfono',
      value: '+1 (555) 123-4567',
      link: 'tel:+15551234567'
    },
    {
      icon: '📍',
      label: 'Ubicación',
      value: 'Puebla, México',
      link: null
    },
    {
      icon: '💼',
      label: 'LinkedIn',
      value: 'linkedin.com/in/tuperfil',
      link: 'https://linkedin.com/in/tuperfil'
    }
  ];

  return (
    <section className='section contact' id='contacto'>
      {/* <h2 className='section__title'> Contacto </h2> */}
      
      <div className='contact__container'>
        {/* Información de contacto */}
        <div className='contact__info'>
          <div className='contact__info-content'>
            <h3 className='contact__info-title'>¡Trabajemos juntos!</h3>
            <p className='contact__info-description'>
              Estoy siempre interesado en nuevas oportunidades y proyectos desafiantes. 
              Si tienes una idea o necesitas ayuda con un proyecto, no dudes en contactarme.
              <br /><br />
              Me pondré en contacto contigo lo antes posible.
            </p>
            
            <div className='contact__details'>
              {contactInfo.map((item, index) => (
                <div key={index} className='contact__detail'>
                  <span className='contact__detail-icon'>{item.icon}</span>
                  <div className='contact__detail-content'>
                    <span className='contact__detail-label'>{item.label}</span>
                    {item.link ? (
                      <a 
                        href={item.link} 
                        className='contact__detail-value link'
                        target={item.link.startsWith('http') ? '_blank' : '_self'}
                        rel={item.link.startsWith('http') ? 'noopener noreferrer' : ''}
                      >
                        {item.value}
                      </a>
                    ) : (
                      <span className='contact__detail-value'>{item.value}</span>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Formulario de contacto */}
        <div className='contact__form-container'>
          <form className='contact__form' onSubmit={handleSubmit}>
            <div className='contact__form-group'>
              <label htmlFor='name' className='contact__form-label'>
                Nombre *
              </label>
              <input
                type='text'
                id='name'
                name='name'
                value={formData.name}
                onChange={handleInputChange}
                className='contact__form-input'
                required
                disabled={formStatus.isSubmitting}
              />
            </div>

            <div className='contact__form-group'>
              <label htmlFor='email' className='contact__form-label'>
                Email *
              </label>
              <input
                type='email'
                id='email'
                name='email'
                value={formData.email}
                onChange={handleInputChange}
                className='contact__form-input'
                required
                disabled={formStatus.isSubmitting}
              />
            </div>

            <div className='contact__form-group'>
              <label htmlFor='subject' className='contact__form-label'>
                Asunto *
              </label>
              <input
                type='text'
                id='subject'
                name='subject'
                value={formData.subject}
                onChange={handleInputChange}
                className='contact__form-input'
                required
                disabled={formStatus.isSubmitting}
              />
            </div>

            <div className='contact__form-group'>
              <label htmlFor='message' className='contact__form-label'>
                Mensaje *
              </label>
              <textarea
                id='message'
                name='message'
                value={formData.message}
                onChange={handleInputChange}
                className='contact__form-textarea'
                rows='6'
                required
                disabled={formStatus.isSubmitting}
              />
            </div>

            {formStatus.message && (
              <div className={`contact__form-status contact__form-status--${formStatus.type}`}>
                {formStatus.message}
              </div>
            )}

            <button
              type='submit'
              className='btn btn--outline contact__form-submit'
              disabled={formStatus.isSubmitting}
            >
              {formStatus.isSubmitting ? 'Enviando...' : 'Enviar Mensaje'}
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contacto;