// portfolio.js - Versión corregida para ProjectPage
import servicesData from './data/services.json'
import projectsData from './data/projects.json'

// Función utilitaria para generar slugs (mantener la existente)
const slugify = (str) =>
  str
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/\s+/g, '-')
    .replace(/[^\w\-]+/g, '')

// Configuración del header (sin cambios)
const header = {
  homepage: '/',
  title: 'ART.WORKS', // AREON.MX , GEO.ENV , MAPWORKS , SENTINEL.MX , ART.WORKS , GEO.MX , SPATIAL.MX , ART.SPACE , ART.GEO , GEO.ART , TERRA.MX , ART.TERRA , TERRANOVA, GEO-METRICS, GEO-MATRIX, TERRA.SCOPE, NATUR.DATA, TERRAFORMEX
} // AUREA, NATRIX , GAIA.MX, POLARIS, STRATA, GEO.ENDURIUM, GEOGLE, AMB.MX, GEO.STR, AEON.MX, SENTIA.MX

// Información personal (sin cambios)
