// Versión Beta_v3.05 - Default Minimal - Se remueven varios bloques para la visualización de las páginas de servicio predeterminadas
// Incorpora nuevas funciones para el renderizado de nuevos campos en la página de 'Cartografía Temática'
// Renderización muy básica para vista default
// Texto desbordado en Fichas 


import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Badge, Card, Button, Modal } from 'react-bootstrap';
import { useParams, Link } from 'react-router-dom';
import { servicios, getProjectsByService, servicesData } from '../portfolio';
import ProjectPDFViewer from '../components/PDFViewer/ProjectPDFViewer';
import ImageComparison from '../components/ImageComparison';
import { 
  FiArrowLeft, FiClock, FiUsers, FiMapPin, FiTrendingUp, 
  FiMap, FiLayers, FiTarget, FiDollarSign, FiZoomIn, FiEye 
} from 'react-icons/fi';
import './ServicePage.css';

const ServicePage = () => {
  const { slug } = useParams();
  const service = servicios.find(s => s.slug === slug);
  const serviceDetails = servicesData.services.find(s => s.navigation.slug === slug);
  const relatedProjects = getProjectsByService(serviceDetails?.id || '');


  useEffect(() => {
    setTimeout(() => window.scrollTo(0, 0), 50) //Aumentar a 100 o 200 si no funciona scroll
  }, [slug])

  if (!service || !serviceDetails) {
    return (
      <Container className="service-page">
        <Row>
          <Col>
            <h1>Servicio no encontrado</h1>
            <Link to="/#servicios" className="btn btn--outline">
              Ver todos los servicios
            </Link>
          </Col>
        </Row>
      </Container>
    );
  }

  // Renderizar layout específico según el servicio
  if (serviceDetails.id === 'cartografia-tematica') {
    return <CartografiaLayout service={service} serviceDetails={serviceDetails} relatedProjects={relatedProjects} />;
  } else {
    return <DefaultLayout service={service} serviceDetails={serviceDetails} relatedProjects={relatedProjects} />;
  }
};

// Layout específico para cartografía (simplificado) (Previamente se usaba project-page (durante desarrollo, revisar compatibilidad))
const CartografiaLayout = ({ service, serviceDetails, relatedProjects }) => {
  return (
    <Container fluid className="service-page"> 
      <Row className="mb-4">
        <Col>
          <Link to="/#servicios" className="btn--back">
            <FiArrowLeft /> regresar a servicios
          </Link>
        </Col>
      </Row>

      {/* Header especial para cartografía */}
      <Row>
        <Col>
          <div className="service-header-cartografia mb-5">
            <div className="service-header-content">
              <Badge bg="primary" className="service-category-badge mb-3">
                <FiMap className="me-2" />
                {serviceDetails.category.toUpperCase()}
              </Badge>
              <h1 className="service-main-title">{service.name}</h1>
              <p className="service-main-description">{service.description}</p>
              <div className="service-header-stats">
                <div className="stat-item">
                  <FiTarget className="stat-icon" />
                  <span className="stat-number">{service.projectCount}</span>
                  <span className="stat-label">Proyectos</span>
                </div>
                <div className="stat-item">
                  <FiUsers className="stat-icon" />
                  <span className="stat-number">{service.sectors?.length || 6}</span>
                  <span className="stat-label">Sectores</span>
                </div>
                <div className="stat-item">
                  <FiClock className="stat-icon" />
                  <span className="stat-number">2-6</span>
                  <span className="stat-label">Semanas</span>
                </div>
              </div>
            </div>
          </div>
        </Col>
      </Row>

      {/* Métricas clave */}
      <Row className="mb-5 metrics-row">
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiLayers className="metric-icon primary" />
            <div className="metric-content">
              {/*<h4 className="metric-number">{serviceDetails.details?.mapTypes?.length || 6}</h4> */}
              <p className="metric-label"> 15+ Tipos de Mapas</p>
              <small className="metric-description">Especializaciones disponibles</small>
            </div>
          </div>
        </Col>
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiMap className="metric-icon success" />
            <div className="metric-content">
              <h4 className="metric-number">1:50K</h4>
              <p className="metric-label">Escala Técnica</p>
              <small className="metric-description">Precisión cartográfica</small>
            </div>
          </div>
        </Col>
        {/*
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiTarget className="metric-icon warning" />
            <div className="metric-content">
              <h4 className="metric-number">95%</h4>
              <p className="metric-label">Precisión</p>
              <small className="metric-description">Validación de campo</small>
            </div>
          </div>
        </Col>
        */}
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiZoomIn className="metric-icon info" />
            <div className="metric-content">
              <h4 className="metric-number">Nacional</h4>
              <p className="metric-label">Cobertura</p>
              <small className="metric-description">Todo México</small>
            </div>
          </div>
        </Col>
      </Row>

      {/* Tipos de mapas */}
      {serviceDetails.details?.mapTypes && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiLayers className="section-icon" />
              Tipos de Mapas Especializados
            </h3>
            <p className="section-subtitle">
              Soluciones cartográficas adaptadas a sus necesidades específicas
            </p>
          </div>
          
          <Row>
            {serviceDetails.details.mapTypes.map((mapType, index) => (
              <Col lg={6} md={6} className="mb-4" key={index}>  {/* Cambiar a lg=4 para mostrar 3 columnas */}
                <Card className="map-type-card h-100">
                  <Card.Body>
                    <div className="map-type-header">
                      <h5 className="map-type-title">{mapType.name}</h5>
                      {/*
                      <Badge bg="outline-primary" className="map-type-badge">
                        Especializado
                      </Badge>                      
                      */}                                  
                    </div>
                    
                    <p className="map-type-description">{mapType.description}</p>
                    
                    <div className="map-type-details">
                      <div className="detail-section">
                        <h6 className="detail-title">
                          <FiTarget className="detail-icon" />
                          Componentes Técnicos
                        </h6>
                        <ul className="detail-list">
                          {mapType.components?.map((component, i) => (
                            <li key={i}>{component}</li>
                          ))}
                        </ul>
                      </div>
                      
                      <div className="detail-section">
                        <h6 className="detail-title">
                          <FiUsers className="detail-icon" />
                          Aplicaciones
                        </h6>
                        <div className="applications-tags">
                          {mapType.applications?.map((app, i) => (
                            <span key={i} className="application-tag">{app}</span>
                          ))}
                        </div>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
      )}

      {/* Galería con zoom funcional */}
      <GallerySection serviceDetails={serviceDetails} />

      {/* Metodología */}
      {service.methodology && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiTarget className="section-icon" />
              Proceso de Trabajo
            </h3>
          </div>
          
          <div className="methodology-timeline-cartografia">
            {service.methodology.map((step, i) => (
              <div key={i} className="methodology-step-cartografia">
                <div className="step-number-cartografia">{i + 1}</div>
                <div className="step-content-cartografia">
                  <h5 className="step-title">Fase {i + 1}</h5>
                  <p className="step-description">{step}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Sectores */}
      {service.sectors && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiUsers className="section-icon" />
              Sectores que Atendemos
            </h3>
          </div>
          
          <Row>
            {service.sectors.map((sector, i) => (
              <Col lg={2} md={4} sm={4} className="mb-3 sector-col" key={i}>
                <div className="sector-card-cartografia">
                  {/*<FiMapPin className="sector-icon" /> */}
                  <span className="sector-name">{sector}</span>
                </div>
              </Col>
            ))}
          </Row>
        </div>
      )}
      
      {/* Precios */}
      {serviceDetails.pricing && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiDollarSign className="section-icon" />
              Pregunta por nuestros Paquetes:
            </h3>
          </div>
          
          <Row>
            {Object.entries(serviceDetails.pricing).map(([key, package_info]) => (
              <Col lg={4} md={6} sm={12} className="mb-4" key={key}> {/* usar sm="auto" para evitar que ocupe todo el ancho de la página en pantallas pequeñas */}
                <Card className={`pricing-card-cartografia ${key === 'standard' ? 'pricing-featured' : ''}`}>
                  {key === 'standard'}
                  
                  <Card.Body className="text-center">
                    <h5 className="pricing-title">{package_info.name}</h5>
                    {/*<div className="pricing-price">{package_info.price}</div> */}
                    <p className="pricing-description">{package_info.description}</p>
                    
                    <ul className="pricing-includes">
                      {package_info.includes?.map((item, i) => (
                        <li key={i}>{item}</li>
                      ))}
                    </ul>
                    
                    <Link to="/#contacto">
                      <Button 
                        className="btn btn--cta-cartografia"
                      >
                        Solicitar Cotización
                      </Button>
                    </Link>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
      )}

      {/* Proyectos relacionados */}
      <ProjectsSection relatedProjects={relatedProjects} />

      {/* CTA */}
      <div className="cartografia-cta mb-5">
        <Container>
          <Row className="align-items-center">
            <Col lg={8}>
              <h4 className="cta-title">¿Necesitas cartografía temática especializada?</h4>
              <p className="cta-description">
                Transformemos sus datos en mapas que comuniquen efectivamente. 
                Contáctenos para una consulta personalizada sin costo.
              </p>
            </Col>
            <Col lg={4} className="text-lg-end">
              <Link to="/#contacto" className="btn btn--cta-cartografia">
                <FiMap className="me-2" />
                Solicitar Consulta Gratuita
              </Link>
            </Col>
          </Row>
        </Container>
      </div>
    </Container>
  );
};

// Componente de galería con zoom funcional
const GallerySection = ({ serviceDetails }) => {
  const [showModal, setShowModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  
  const galleryExamples = serviceDetails?.details?.galleryExamples || [];
  
  if (!galleryExamples.length) return null;
  
  return (
    <>
      <div className="cartografia-section mb-5">
        <div className="section-header">
          <h3 className="section-title">
            <FiMap className="section-icon" />
            Galería de Diseños
          </h3>
          <p className="section-subtitle">
            Ejemplos de productos cartográficos entregados a clientes
          </p>
        </div>
        
        <Row>
          {galleryExamples.map((example, index) => (
            <Col lg={4} md={6} className="mb-4" key={index}> {/*Cambiar lg=6 para obtener 2 columnas */}
              <Card className="gallery-card h-100">
                <div 
                  className="gallery-image-container"
                  onClick={() => {
                    setSelectedImage(example);
                    setShowModal(true);
                  }}
                  style={{ cursor: 'pointer' }}
                >
                  <Card.Img 
                    variant="top" 
                    src={example.imageUrl} 
                    alt={example.title}
                    className="gallery-image"
                    onError={(e) => {
                      e.target.onerror = null;
                      e.target.src = '/images/empty.png';
                    }}
                  />
                  <div className="gallery-overlay">
                    <Button 
                      variant="light" 
                      size="sm" 
                      className="gallery-zoom-btn"
                    >
                      <FiEye className="me-1" /> Ver detalle
                    </Button>
                  </div>
                </div>
                
                <Card.Body>
                  <Card.Title className="gallery-title">{example.title}</Card.Title>
                  <Card.Text className="gallery-description">
                    {example.description}
                  </Card.Text>
                  {/*}
                  <div className="gallery-design-details">
                    <h6 className="design-details-title">Elementos de diseño:</h6>
                    <p className="design-details-text">{example.design}</p>
                  </div>
                  */}
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </div>
      
      {/* Modal para zoom */}
      <Modal show={showModal} onHide={() => setShowModal(false)} size="lg" centered>
        <Modal.Header closeButton>
          <Modal.Title>{selectedImage?.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {selectedImage && (
            <>
              <img 
                src={selectedImage.imageUrl} 
                alt={selectedImage.title}
                className="img-fluid"
                style={{ maxHeight: '70vh', objectFit: 'contain' }}
              />
              <div className="mt-3">
                <p className="text-muted">{selectedImage.description}</p>
                <small className="text-muted">
                  <strong>Elementos de diseño:</strong> {selectedImage.design}
                </small>
              </div>
            </>
          )}
        </Modal.Body>
      </Modal>
    </>
  );
};


// Componente de proyectos relacionados
const ProjectsSection = ({ relatedProjects }) => {
  if (!relatedProjects.length) return null;
  
  return (
    <div className="cartografia-section mb-5">
      <div className="section-header">
        <h3 className="section-title">
          <FiTrendingUp className="section-icon" />
          Casos de Estudio
        </h3>
      </div>
      
      <Row>
        {relatedProjects.map((project, i) => (
          <Col md={6} className="mb-3" key={i}>
            <Card className="project-card h-100">
              <Card.Body>
                <div className="d-flex justify-content-between align-items-start mb-2">
                  <Badge bg="outline-secondary" className="mb-2">
                    {project.year}
                  </Badge>
                  <Badge 
                    bg={
                      project.status === 'completado' ? 'success' :
                      project.status === 'activo' ? 'primary' : 'warning'
                    }
                    className="mb-2"
                  >
                    {project.status}
                  </Badge>
                </div>
                <Card.Title className="h6">{project.name}</Card.Title>
                <Card.Text className="text-muted">
                  {project.description.substring(0, 120)}...
                </Card.Text>
                <Link 
                  to={`/${project.navigation?.detailUrl || `proyectos/${project.slug}`}`}
                  className="btn btn--outline btn-sm"
                >
                  Ver caso de estudio
                </Link>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

// Layout por defecto para otros servicios - USANDO MISMO DISEÑO
const DefaultLayout = ({ service, serviceDetails, relatedProjects }) => {
  return (
    
    <Container fluid className="service-page">
     {/*<Container fluid className="project-page">*/}
     {/*<Container classname="service-page-container">*/}
      <Row className="mb-4">
        <Col>
          <Link to="/#servicios" className="btn--back">
            <FiArrowLeft /> regresar a servicios
          </Link>
        </Col>
      </Row>

      {/* Header USANDO MISMAS CLASES */}
      <Row>
        <Col>
          <div className="service-header-cartografia mb-5">
            <div className="service-header-content">
              {/*    
                <Badge bg="primary" className="service-category-badge mb-3">
                <FiMap className="me-2" />
                {serviceDetails.category.toUpperCase()}
              </Badge>              
               */}
              {/*           */}

              <h1 className="service-main-title">{service.name}</h1>
              <p className="service-main-description">{service.description}</p>

              {/*
                <div className="service-header-stats">
                  <div className="stat-item">
                    <FiClock className="stat-icon" />
                    <span className="stat-number">{service.projectCount || 0}</span>
                    <span className="stat-label">Proyectos</span>
                  </div>
                  <div className="stat-item">
                    <FiUsers className="stat-icon" />
                    <span className="stat-number">{service.sectors?.length || 0}</span>
                    <span className="stat-label">Sectores</span>
                  </div>
                  <div className="stat-item">
                    <FiTarget className="stat-icon" />
                    <span className="stat-number">{service.estimatedDuration || 'Variable'}</span>
                    <span className="stat-label">Duración</span>
                  </div>
                </div>                  
                     */}              
            </div>
          </div>
        </Col>
      </Row>

      {/* Métricas clave USANDO MISMAS CLASES 
      <Row className="mb-5">
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiLayers className="metric-icon primary" />
            <div className="metric-content">
              <h4 className="metric-number">{service.deliverables?.length || 4}</h4>
              <p className="metric-label">Entregables</p>
              <small className="metric-description">Productos incluidos</small>
            </div>
          </div>
        </Col>
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiMap className="metric-icon success" />
            <div className="metric-content">
              <h4 className="metric-number">{service.stack?.filter(s => s && s.trim()).length || 3}</h4>
              <p className="metric-label">Herramientas</p>
              <small className="metric-description">Tecnologías utilizadas</small>
            </div>
          </div>
        </Col>
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiTarget className="metric-icon warning" />
            <div className="metric-content">
              <h4 className="metric-number">100%</h4>
              <p className="metric-label">Satisfacción</p>
              <small className="metric-description">Clientes satisfechos</small>
            </div>
          </div>
        </Col>
        <Col md={3} sm={6} className="mb-3">
          <div className="cartografia-metric-card">
            <FiZoomIn className="metric-icon info" />
            <div className="metric-content">
              <h4 className="metric-number">Nacional</h4>
              <p className="metric-label">Cobertura</p>
              <small className="metric-description">Todo México</small>
            </div>
          </div>
        </Col>
      </Row>
      */}


      {/* Entregables USANDO MISMAS CLASES */}
      {service.deliverables && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiTarget className="section-icon" />
              Productos y Entregables
            </h3>
          </div>
          <Row>
            {service.deliverables.map((deliverable, i) => (
              <Col md={6} className="mb-2" key={i}>
                <div className="deliverable-item">
                  <span className="deliverable-bullet">•</span>
                  {deliverable}
                </div>
              </Col>
            ))}
          </Row>
        </div>
      )}

      {/* Metodología USANDO MISMAS CLASES */}
      {service.methodology && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiTarget className="section-icon" />
              Proceso de Trabajo
            </h3>
          </div>
          
          <div className="methodology-timeline-cartografia">
            {service.methodology.map((step, i) => (
              <div key={i} className="methodology-step-cartografia">
                <div className="step-number-cartografia">{i + 1}</div>
                <div className="step-content-cartografia">
                  <h5 className="step-title">Fase {i + 1}</h5>
                  <p className="step-description">{step}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Secciones - Para Evaluación de Impacto Ambiental */}
      {serviceDetails.overview?.metodos && 
      serviceDetails.id === 'evaluacion-impacto' && 
      serviceDetails.overview.metodos.filter(met => met && met.trim() !== '').length > 0 && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiLayers className="section-icon" />
              Herramientas de Análisis
            </h3>
          </div>
          <div className="applications-tags">
            {serviceDetails.overview.metodos.filter(met => met && met.trim() !== '').map((met, i) => (
              <span key={i} className="application-tag">
                {met}
              </span>
            ))}
          </div>
        </div>
      )}

      {/* Tecnologías para servicios técnicos */}
      {service.stack && 
      serviceDetails.id !== 'tramites-ambientales' && 
      service.stack.filter(tech => tech && tech.trim() !== '').length > 0 && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiLayers className="section-icon" />
              Software
            </h3>
          </div>
          <div className="applications-tags">
            {service.stack.filter(tech => tech && tech.trim() !== '').map((tech, i) => (
              <span key={i} className="application-tag">
                {tech}
              </span>
            ))}
          </div>
        </div>
      )}

      {/* Dependencias para trámites - USANDO DATOS RAW */}
      {serviceDetails.overview?.dependencias && 
      serviceDetails.id === 'tramites-ambientales' && 
      serviceDetails.overview.dependencias.filter(dep => dep && dep.trim() !== '').length > 0 && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiLayers className="section-icon" />
              Dependencias
            </h3>
          </div>
          <div className="applications-tags">
            {serviceDetails.overview.dependencias.filter(dep => dep && dep.trim() !== '').map((dep, i) => (
              <span key={i} className="application-tag">
                {dep}
              </span>
            ))}
          </div>
        </div>
      )}

      {/* Sectores USANDO MISMAS CLASES */}
      {service.sectors && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiUsers className="section-icon" />
              Sectores que Atendemos
            </h3>
          </div>
          
          <Row>
            {service.sectors.map((sector, i) => (
              <Col lg={2} md={4} sm={4} className="mb-3 sector-col" key={i}>
                <div className="sector-card-cartografia">
                  <span className="sector-name">{sector}</span>
                </div>
              </Col>
            ))}
          </Row>
        </div>
      )}

      {/* Proyectos relacionados */}
      <ProjectsSection relatedProjects={relatedProjects} />

      {/* Comparador para análisis satelital */}
      {serviceDetails.id === 'analisis-satelital' && (
        <div className="cartografia-section mb-5">
          <div className="section-header">
            <h3 className="section-title">
              <FiEye className="section-icon" />
              Ejemplos Interactivos
            </h3>
            <p className="section-subtitle">
              Compare resultados de procesamiento satelital
            </p>
          </div>
          <ImageComparison
            beforeImage="images/satelital/b1.jpg"  // https://picsum.photos/800/600?random=1"
            afterImage="images/satelital/a1.jpg"   //https://picsum.photos/800/600?random=2"
            beforeLabel="Imagen Original RGB"
            afterLabel="Análisis NDVI Procesado"
            title="Comparación: RGB vs NDVI"
          />

          {/* BOTÓN - Más ejemplos satelitales */}
          <div className="text-center mt-4">
            <Link to="/ejemplos-satelitales" className="btn--back">
              <FiEye className="me-2" />
              Ver más ejemplos
            </Link>
          </div>
        </div>
      )}


      {/* CTA USANDO MISMAS CLASES */}
      <div className="cartografia-cta mb-5">
        <Container>
          <Row className="align-items-center">
            <Col lg={8}>
              <h4 className="cta-title">¿Interesado en este servicio?</h4>
              <p className="cta-description">
                Contáctame para discutir cómo podemos aplicar estas soluciones a tu proyecto específico.
              </p>
            </Col>
            <Col lg={4} className="text-lg-end">
              <Link to="/#contacto" className="btn btn--cta-cartografia">
                <FiMap className="me-2" />
                Solicitar Consulta Gratuita
              </Link>
            </Col>
          </Row>
        </Container>
      </div>
    </Container>
  );
};

export default ServicePage;