import GitHubIcon from '@material-ui/icons/GitHub'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import { about } from '../../portfolio'
import {FiBookOpen, FiUser} from 'react-icons/fi'
import './About.css'

// Agregué esto para la pagina de CV

import { Link } from 'react-router-dom'

const About = () => {
  const { name, role, description, resume, social } = about

  return (
    <div className='about center'>
      {name && (
        <h1>
          Hola, soy  <br />
          <span className='about__name'>{name}</span>
        </h1>
      )}

      {/*{role && <h2 className='about__role'> - {role} -</h2>} */}

      {/* opción 1:
       {about.role && about.role.split(',').map((role, index) => (
        <h2 key={index} className='about__role mb-0'>- {role.trim()} -</h2>
      ))}
      */}

      {/* opción 3: 
      {about.role && about.role.split(',').map((role, index, arr) => (
        <h2
          key={index}
          className={`about__role ${index !== arr.length - 1 ? 'mb-1' : 'mb-0'}`}
        >
          - {role.trim()} -
        </h2>
      ))}
      */}

      {about.role && (
        <div className="flex flex-col gap-[2px] mt-4">
          {about.role.split(',').map((role, index) => (
            <h2 key={index} className="text-base leading-tight m-0.5 p-0.5 font-normal">
              - {role.trim()} -
            </h2>
          ))}
        </div>
      )}

      
      <p className='about__desc'>{description && description}</p>

      <div className='about__contact center'>

        <Link to="/catalogo-servicios">
          <span type='button' className='btn btn--outline'>
            <FiBookOpen className="me-2" />
            <span className="btn-text-full">catálogo digital</span>
            <span className="btn-text-short">catálogo</span>
          </span>
        </Link>

        <Link to="/cv-pdf">
          <span type='button' className='btn btn--outline'>
           <FiUser className="me-2" />
            CV
          </span>
        </Link>
        
        {social && (
          <>
            {social.github && (
              <a
                href={social.github}
                aria-label='github'
                className='link link--icon'
              >
                <GitHubIcon />
              </a>
            )}

            {social.linkedin && (
              <a
                href={social.linkedin}
                aria-label='linkedin'
                className='link link--icon'
              >
                <LinkedInIcon />
              </a>
            )}
          </>
        )}
      </div>
    </div>
  )
}

export default About
