import React, { useContext, useEffect } from 'react'; // ← Agregar useEffect aquíimport { Container, Row, Col } from 'react-bootstrap';
import { Container, Row, Col } from 'react-bootstrap'; // ← Faltaba salto de línea
import { Link } from 'react-router-dom';
import { ThemeContext } from '../contexts/theme';
import PDFViewer from '../components/PDFViewer/PDFViewer_main';
import { FiArrowLeft, FiDownload } from 'react-icons/fi';
import './Catalogo.css';

const CatPDFViewerPage = () => {

  const [{ themeName }] = useContext(ThemeContext);

    // Scroll to top al cargar la página
    useEffect(() => {
      setTimeout(() => window.scrollTo(0, 0), 50);
    }, []); // Array vacío porque no hay slug en esta página
  
  
  // URL del PDF del catálogo
  const pdfUrl = 'pdfs/portafolio_servicios_ambientales.pdf';
  
  return (
    <div className={`pdf-servicios-page ${themeName}`}>
      {/* Header minimalista */}
      <div className="pdf-servicios-header">
        <Container>
          <Row className="align-items-center">
            <Col xs={6} md={4}>
              <Link to="/#servicios" className="btn-back-minimal">
                <FiArrowLeft />
                <span className="back-text">Servicios</span>
              </Link>
            </Col>
            
            <Col xs={12} md={4} className="text-center order-3 order-md-2">
              <h1 className="page-title">Catálogo </h1>
            </Col>
            
            <Col xs={6} md={4} className="text-end order-2 order-md-3">
              <a 
                href={pdfUrl} 
                download="Catalogo_Servicios_Ambientales.pdf"
                className="btn-download-minimal"
                title="Descargar catálogo"
              >
                <FiDownload />
                <span className="download-text">Descargar Catálogo</span>
              </a>
            </Col>
          </Row>
        </Container>
      </div>

      {/* Contenedor del PDF */}
      <div className="pdf-servicios-content">
        <Container fluid className="pdf-container">
          <Row className="justify-content-center">
            <Col>
              <PDFViewer 
                pdfUrl={pdfUrl}
                showDownload={false} // Ya tenemos botón en header
                initialViewMode="scroll"
                initialScale={1.1}
              />
            </Col>
          </Row>
        </Container>
      </div>

      {/* Footer opcional con acciones adicionales */}
      <div className="pdf-servicios-footer">
        <Container>
          <Row className="justify-content-center">
            <Col md={8} className="text-center">
              <p className="footer-text">
                ¿Te interesa algún servicio?
                <Link to="/#contacto" className="contact-link">
                  Contáctame
                </Link>
              </p>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default CatPDFViewerPage;