// ============================================
// PDFViewer LIMPIO - Sin Widescreen, Bugs Corregidos
// ============================================

import React, { useState, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { Button } from 'react-bootstrap';
import { 
  FiMaximize, FiMinimize, FiX, FiZoomIn, FiZoomOut, 
  FiChevronLeft, FiChevronRight, FiList, FiAlignJustify,
  FiLayers, FiSquare, FiMaximize2, FiFile, // ← Nuevos íconos
  FiRotateCw, FiRefreshCw, FiDownload,
  FiScissors
} from 'react-icons/fi';
import './PDFViewer_main.css';

// Configuración necesaria para react-pdf
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

const PDFViewer = ({ 
  pdfUrl, 
  title = null,
  showDownload = false,
  initialViewMode = 'scroll',
  initialScale = 1.0 
}) => {
  const [numPages, setNumPages] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [viewMode, setViewMode] = useState(initialViewMode);
  const [scale, setScale] = useState(initialScale);
  const [rotation, setRotation] = useState(0);
  const [showControls, setShowControls] = useState(true);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
    setLoading(false);
  }

  const toggleFullscreen = () => {
    setIsFullscreen(!isFullscreen);
    if (!isFullscreen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
  };

  const adjustZoom = (delta) => {
    setScale(prevScale => Math.max(0.5, Math.min(3.0, prevScale + delta)));
  };

  const rotateDocument = () => {
    setRotation(prev => (prev + 90) % 360);
  };

  const resetView = () => {
    setScale(initialScale);
    setRotation(0);
    setCurrentPage(1);
  };

  const changePage = (offset) => {
    const newPage = currentPage + offset;
    if (newPage >= 1 && newPage <= numPages) {
      setCurrentPage(newPage);
    }
  };

  // Auto-hide controls en fullscreen
  useEffect(() => {
    if (!isFullscreen) return;

    let timeout;
    
    const showControlsTemporarily = () => {
      setShowControls(true);
      clearTimeout(timeout);
      timeout = setTimeout(() => setShowControls(false), 2000); // Más tiempo en móvil
    };

    // Detectar tanto mouse (desktop) como touch (móvil)
    const handleMouseMove = showControlsTemporarily;
    const handleTouchStart = showControlsTemporarily;
    const handleTouchMove = showControlsTemporarily;

    // Agregar todos los event listeners
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('touchstart', handleTouchStart);
    document.addEventListener('touchmove', handleTouchMove);
    
    // Timer inicial
    timeout = setTimeout(() => setShowControls(false), 3000);

    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('touchstart', handleTouchStart);
      document.removeEventListener('touchmove', handleTouchMove);
      clearTimeout(timeout);
    };
  }, [isFullscreen]);

  // Navegación con teclado
  useEffect(() => {
    const handleKeyDown = (event) => {
      // Solo funcionar en vista paginada
      if (viewMode !== 'paged') return;
      
      switch (event.key) {
        case 'ArrowLeft':
        case 'ArrowUp':
          event.preventDefault();
          changePage(-1);
          break;
        case 'ArrowRight':
        case 'ArrowDown':
          event.preventDefault();
          changePage(1);
          break;
        case 'Home':
          event.preventDefault();
          setCurrentPage(1);
          break;
        case 'End':
          event.preventDefault();
          if (numPages) setCurrentPage(numPages);
          break;
        default:
          break;
      }
    };

    // Solo agregar listener si estamos en vista paginada
    if (viewMode === 'paged') {
      document.addEventListener('keydown', handleKeyDown);
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [viewMode, currentPage, numPages]); // Dependencias

  // Escape key handler
  useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === 'Escape' && isFullscreen) {
        setIsFullscreen(false);
        document.body.style.overflow = '';
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = '';
    };
  }, [isFullscreen]);

  const containerClass = isFullscreen 
    ? "pdf-viewer pdf-viewer--fullscreen" 
    : "pdf-viewer";

  return (
    <>
      <div className={containerClass}>
        {/* Header con controles - Solo en vista normal */}
        {!isFullscreen && (
          <div className="pdf-toolbar">
            {title && <h3 className="pdf-title">{title}</h3>}
            
            <div className="pdf-controls-bar">
              {/* Grupo de modo de vista */}
              <div className="control-group">
                <Button
                  className={`btn-toolbar ${viewMode === 'scroll' ? 'btn-toolbar--active' : ''}`}
                  onClick={() => setViewMode('scroll')}
                  title="Vista continua"
                >
                  <FiLayers />
                </Button>
                <Button
                  className={`btn-toolbar ${viewMode === 'paged' ? 'btn-toolbar--active' : ''}`}
                  onClick={() => setViewMode('paged')}
                  title="Vista paginada"
                >
                  <FiFile />
                </Button>
              </div>

              {/* Navegación - Solo en modo paginado */}
              {viewMode === 'paged' && (
                <div className="control-group">
                  <Button
                    className="btn-toolbar"
                    onClick={() => changePage(-1)}
                    disabled={currentPage <= 1}
                    title="Página anterior"
                  >
                    <FiChevronLeft />
                  </Button>
                  <span className="page-info">
                    {currentPage}/{numPages || '...'}
                  </span>
                  <Button
                    className="btn-toolbar"
                    onClick={() => changePage(1)}
                    disabled={!numPages || currentPage >= numPages}
                    title="Página siguiente"
                  >
                    <FiChevronRight />
                  </Button>
                </div>
              )}

              {/* Zoom y rotación - OCULTO EN MÓVIL */}
              <div className="control-group control-group--desktop-only">
                <Button
                  className="btn-toolbar"
                  onClick={() => adjustZoom(-0.2)}
                  title="Reducir zoom"
                >
                  <FiZoomOut />
                </Button>
                <span className="zoom-info">
                  {Math.round(scale * 100)}%
                </span>
                <Button
                  className="btn-toolbar"
                  onClick={() => adjustZoom(0.2)}
                  title="Aumentar zoom"
                >
                  <FiZoomIn />
                </Button>
                <Button
                  className="btn-toolbar"
                  onClick={rotateDocument}
                  title="Rotar 90°"
                >
                  <FiRotateCw />
                </Button>
                <Button
                  className="btn-toolbar"
                  onClick={resetView}
                  title="Resetear vista"
                >
                  <FiRefreshCw />
                </Button>
              </div>

              {/* Acciones principales - SIEMPRE VISIBLE */}
              <div className="control-group control-group--always-visible">
                {showDownload && (
                  <Button
                    className="btn-toolbar"
                    onClick={() => window.open(pdfUrl, '_blank')}
                    title="Descargar PDF"
                  >
                    <FiDownload />
                  </Button>
                )}
                <Button
                  className="btn-toolbar btn-toolbar--primary"
                  onClick={toggleFullscreen}
                  title="Pantalla completa"
                >
                  <FiMaximize2 />
                </Button>
              </div>
            </div>
          </div>
        )}

        {/* Controles flotantes - Solo en fullscreen */}
        {isFullscreen && (
          <div className={`pdf-floating-controls ${showControls ? 'pdf-floating-controls--visible' : 'pdf-floating-controls--hidden'}`}>
            {/* DESKTOP: Una sola línea compacta */}
            <div className="floating-controls-desktop">
              <div className="control-section">
                <Button
                  className={`btn-floating ${viewMode === 'scroll' ? 'btn-floating--active' : ''}`}
                  onClick={() => setViewMode('scroll')}
                  title="Vista continua"
                >
                  <FiLayers />
                </Button>
                <Button
                  className={`btn-floating ${viewMode === 'paged' ? 'btn-floating--active' : ''}`}
                  onClick={() => setViewMode('paged')}
                  title="Vista paginada"
                >
                  <FiFile />
                </Button>
                
                {viewMode === 'paged' && (
                  <>
                    <span className="separator" />
                    <Button
                      className="btn-floating"
                      onClick={() => changePage(-1)}
                      disabled={currentPage <= 1}
                    >
                      <FiChevronLeft />
                    </Button>
                    <span className="page-indicator">
                      {currentPage}/{numPages || '...'}
                    </span>
                    <Button
                      className="btn-floating"
                      onClick={() => changePage(1)}
                      disabled={!numPages || currentPage >= numPages}
                    >
                      <FiChevronRight />
                    </Button>
                    <span className="separator" />
                  </>
                )}
                
                <Button
                  className="btn-floating"
                  onClick={() => adjustZoom(-0.2)}
                >
                  <FiZoomOut />
                </Button>
                <span className="zoom-indicator">
                  {Math.round(scale * 100)}%
                </span>
                <Button
                  className="btn-floating"
                  onClick={() => adjustZoom(0.2)}
                >
                  <FiZoomIn />
                </Button>
                <span className="separator" />
                <Button
                  className="btn-floating"
                  onClick={rotateDocument}
                >
                  <FiRotateCw />
                </Button>
                <Button
                  className="btn-floating"
                  onClick={resetView}
                >
                  <FiRefreshCw />
                </Button>
              </div>
            </div>

            {/* MÓVIL: Bottom bar simplificado */}
            <div className="floating-controls-mobile">
              <div className="mobile-nav-section">
                <Button
                  className={`btn-mobile ${viewMode === 'scroll' ? 'btn-mobile--active' : ''}`}
                  onClick={() => setViewMode('scroll')}
                >
                  <FiLayers />
                </Button>
                <Button
                  className={`btn-mobile ${viewMode === 'paged' ? 'btn-mobile--active' : ''}`}
                  onClick={() => setViewMode('paged')}
                >
                  <FiFile />
                </Button>
                
                {viewMode === 'paged' && (
                  <>
                    <Button
                      className="btn-mobile"
                      onClick={() => changePage(-1)}
                      disabled={currentPage <= 1}
                    >
                      <FiChevronLeft />
                    </Button>
                    <span className="mobile-page-info">
                      {currentPage}/{numPages || '...'}
                    </span>
                    <Button
                      className="btn-mobile"
                      onClick={() => changePage(1)}
                      disabled={!numPages || currentPage >= numPages}
                    >
                      <FiChevronRight />
                    </Button>
                  </>
                )}
                
                <Button
                  className="btn-mobile"
                  onClick={rotateDocument}
                >
                  <FiRotateCw />
                </Button>
                {/*
                <Button
                  className="btn-mobile"
                  onClick={resetView}
                >
                  <FiRefreshCw />
                </Button>
                */}
                {/* Botón fullscreen siempre visible en móvil */}
                <Button
                  className="btn-mobile btn-mobile--primary"
                  onClick={toggleFullscreen}
                  title="Pantalla completa"
                >
                  <FiMaximize />
                </Button>
              </div>
            </div>
          </div>
        )}

        {/* Botón cerrar fullscreen */}
        {isFullscreen && (
          <Button 
            className="btn-close-fullscreen"
            onClick={toggleFullscreen}
            title="Cerrar (Esc)"
          >
            <FiX />
          </Button>
        )}

        {/* Contenido del PDF */}
        <div className="pdf-content">
          {loading && <div className="pdf-loading">Cargando documento...</div>}
          
          <div className="pdf-document-container">
            <Document
              file={pdfUrl}
              onLoadSuccess={onDocumentLoadSuccess}
              onLoadError={(error) => console.error('Error:', error)}
              loading={<div className="pdf-loading">Cargando...</div>}
              error={<div className="pdf-error">Error al cargar PDF</div>}
            >
              {viewMode === 'scroll' ? (
                // Modo scroll: todas las páginas
                <div className="pdf-scroll-view">
                  {Array.from(new Array(numPages), (el, index) => (
                    <div key={`page_${index + 1}`} className="pdf-page-container">
                      <Page 
                        pageNumber={index + 1}
                        scale={scale}
                        rotate={rotation}
                        className="pdf-page"
                        renderTextLayer={false}
                        renderAnnotationLayer={false}
                        width={window.innerWidth < 768 ? window.innerWidth - 40 : undefined}
                      />
                    </div>
                  ))}
                </div>
              ) : (
                // Modo paginado: una página
                <div className="pdf-paged-view">
                  <Page 
                    pageNumber={currentPage}
                    scale={scale}
                    rotate={rotation}
                    className="pdf-page"
                    renderTextLayer={false}
                    renderAnnotationLayer={false}
                    width={window.innerWidth < 768 ? window.innerWidth - 40 : undefined}
                  />
                </div>
              )}
            </Document>
          </div>
        </div>
      </div>

      {/* Overlay fullscreen */}
      {isFullscreen && <div className="pdf-overlay" />}
    </>
  );
};

export default PDFViewer;