/*
Versión: 1.B - Bloquea el desplazamiento vertical mientras se presiona el slider del hanger del componente de Comparación de Imagenes

*/
// ImageComparison.js
import React, { useState, useRef, useEffect } from 'react';
import './ImageComparison.css';

const ImageComparison = ({ 
  beforeImage, 
  afterImage, 
  beforeLabel = "Antes", 
  afterLabel = "Después",
  title,
  className = "",
  initialPosition = 50 
}) => {
  const [sliderPosition, setSliderPosition] = useState(initialPosition);
  const [isDragging, setIsDragging] = useState(false);
  const containerRef = useRef(null);
  const sliderRef = useRef(null);

  const disableBodyScroll = () => {
    if (window.innerWidth <= 768) {
      // Solo bloquear overflow, sin position fixed
      document.body.style.overflow = 'hidden';
      // Pero también prevenir touch events en el container padre
      document.body.style.touchAction = 'none';
    }
  };

  const enableBodyScroll = () => {
    if (window.innerWidth <= 768) {
      document.body.style.overflow = '';
      document.body.style.touchAction = '';
    }
  };

  // Función para actualizar posición basada en coordenadas del mouse/touch
  const updatePosition = (clientX) => {
    if (!containerRef.current) return;
    
    const rect = containerRef.current.getBoundingClientRect();
    const x = clientX - rect.left;
    const percentage = Math.max(0, Math.min(100, (x / rect.width) * 100));
    setSliderPosition(percentage);
  };

  // En ImageComparison.js - modificar los event handlers
  const handleMouseDown = (e) => {
  // Solo activar si es el handle en móvil
  if (window.innerWidth <= 576) {
    if (!e.target.closest('.slider-handle')) {
      return; // No hacer nada si no es el handle
    }
  }
  setIsDragging(true);
  updatePosition(e.clientX);
  };

    {/*
    // Event handlers para mouse
    const handleMouseDown = (e) => {
      setIsDragging(true);
      updatePosition(e.clientX);
    };    
      */}

  const handleMouseMove = (e) => {
    if (!isDragging) return;
    updatePosition(e.clientX);
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  // Event handlers para touch (móvil)
  // Ignora desplazamiento con tacto sobre imagen, solo para slider:
  const handleTouchStart = (e) => {
    if (window.innerWidth <= 768) {
      if (!e.target.closest('.slider-handle')) {
        return;
      }
    }
    setIsDragging(true);
    updatePosition(e.touches[0].clientX);
  };

  /* // Original, permite desplazamiento al tacto sobre imagen
  const handleTouchStart = (e) => {
    setIsDragging(true);
    updatePosition(e.touches[0].clientX);
  };
  */

  const handleTouchMove = (e) => {
    if (!isDragging) return;
    e.preventDefault();
    updatePosition(e.touches[0].clientX);
  };

  const handleTouchEnd = () => {
    setIsDragging(false);
  };

  // Agregar event listeners globales
  useEffect(() => {
    const handleGlobalMouseMove = (e) => handleMouseMove(e);
    const handleGlobalMouseUp = () => handleMouseUp();
    const handleGlobalTouchMove = (e) => handleTouchMove(e);
    const handleGlobalTouchEnd = () => handleTouchEnd();

    if (isDragging) {
      document.addEventListener('mousemove', handleGlobalMouseMove);
      document.addEventListener('mouseup', handleGlobalMouseUp);
      document.addEventListener('touchmove', handleGlobalTouchMove, { passive: false });
      document.addEventListener('touchend', handleGlobalTouchEnd);
      
      // 🆕 BLOQUEAR SCROLL EN MÓVIL
      disableBodyScroll();
    }

    return () => {
      document.removeEventListener('mousemove', handleGlobalMouseMove);
      document.removeEventListener('mouseup', handleGlobalMouseUp);
      document.removeEventListener('touchmove', handleGlobalTouchMove);
      document.removeEventListener('touchend', handleGlobalTouchEnd);
      
      // 🆕 RESTAURAR SCROLL EN MÓVIL
      enableBodyScroll();
    };
  }, [isDragging]);

  return (
    <div className={`image-comparison ${className}`}>
      {title && <h3 className="comparison-title">{title}</h3>}
      
      <div 
        className="comparison-container"
        ref={containerRef}
        onMouseDown={handleMouseDown}
        onTouchStart={handleTouchStart}
      >
        {/* Imagen de fondo (antes) */}
        <div className="comparison-image">
          <img src={beforeImage} alt={beforeLabel} />
        </div>
        
        {/* Imagen de primer plano (después) */}
        <div 
          className="comparison-image image-after"
          style={{ clipPath: `inset(0 ${100 - sliderPosition}% 0 0)` }}
        >
          <img src={afterImage} alt={afterLabel} />
        </div>

        {/* Línea divisora con handle */}
        <div 
          className="divider-line"
          style={{ left: `${sliderPosition}%` }}
        >
          <div className={`slider-handle ${isDragging ? 'dragging' : ''}`}>
            <div className="slider-arrows">
              <div className="arrow arrow-left"></div>
              <div className="arrow arrow-right"></div>
            </div>
          </div>
        </div>

        {/* Labels */}
        <div className="comparison-labels">
          <span className="label label-before">{beforeLabel}</span>
          <span className="label label-after">{afterLabel}</span>
        </div>

        {/* Input range invisible para accesibilidad */}
        <input
          ref={sliderRef}
          type="range"
          min="0"
          max="100"
          value={sliderPosition}
          onChange={(e) => setSliderPosition(Number(e.target.value))}
          className="slider-input"
          aria-label={`Comparar ${beforeLabel} con ${afterLabel}`}
        />
      </div>
    </div>
  );
};

export default ImageComparison;