import { useContext } from 'react'
import { ThemeContext } from './contexts/theme'
import 'bootstrap/dist/css/bootstrap.min.css'; // XR: Antes de los estilos personalizados
import './components/PDFViewer/pdfStyleFixes.css'; // XR: Importa las correcciones de estilo para PDF
import About from './components/About/About'
import Services from './components/Services/Services'
import Projects from './components/Projects/Projects'
import Skills from './components/Skills/Skills'
import ScrollToTop from './components/ScrollToTop/ScrollToTop'
import Contact from './components/Contact/Contact'
import Contacto from './components/Contact/Contacto' // El nuevo componente

import './App.css'

const App = () => {
  const [{ themeName }] = useContext(ThemeContext)

  return (
    <div id='top' className={`${themeName} app`}>
      <main>
        <About />
        <Services />
        <Projects />
        <Skills />
        <Contact />
        <Contacto />
      </main>

      <ScrollToTop />
    </div>
  )
}

export default App