import uniqid from 'uniqid'
import { projects } from '../../portfolio'
import './Projects.css'
import {FiEye, FiMoreVertical} from 'react-icons/fi'


// Mapeo de imágenes por servicio
const projectImages = {
  'mapa-cobertura-forestal-michoacan': '/images/proyectos/proyecto_forestal.jpg',
  'deteccion-cambios-landsat-bajio': '/images/proyectos/proyecto_cambios_landsat.png', 
  'atlas-riesgo-volcanico-colima': '/images/proyectos/proyecto_riesgo_volcanico.jpg',
  'monitoreo-calidad-agua-sentinel': '/images/proyectos/proyecto_agua_sentinel.jpg',
  'ordenamiento-territorial': 'images/proyectos/proyecto_ordenamiento_territorial.jpg'
}

const Projects = () => {
  if (!projects.length) return null

  return (
    <section id='proyectos' className='section projects'>
      <h2 className='section__title'>Proyectos</h2>

      <div className='projects__grid'>
        {projects.map((project) => (
          <ProjectCard 
            key={uniqid()} 
            project={project} 
            image={projectImages[project.slug]}
          />
        ))}
      </div>
    </section>
  )
}

const ProjectCard = ({ project, image }) => {
  return (
    <div className='project-card'>
      {/* Estado por defecto: Imagen + Título */}
      <div className='project-card__default'>
        <div 
          className='project-card__image'
          style={{ 
            backgroundImage: image 
              ? `url(${image})` 
              : 'linear-gradient(135deg, var(--clr-primary), rgba(41, 120, 181, 0.8))'
          }}
        >
          {/* Ícono fijo arriba a la derecha */}
          <FiMoreVertical className="project-card__icon" />
        </div>

        <div className='project-card__header'>
          <h3 className='project-card__title'>{project.name}</h3>
        </div>
      </div>

      {/* Estado hover/activo: Texto completo */}
      <div className='project-card__details'>
        <div className='project-card__content'>
          <h3 className='project-card__title'>{project.name}</h3>
          
          <p className='project-card__description'>{project.description}</p>

          {project.stack && project.stack.length > 0 && (
            <div className='project-card__stack'>
              {project.stack
                .filter(item => item && item.trim() !== '')
                .slice(0, 4)
                .map((item) => (
                  <span key={uniqid()} className='project-card__tech'>
                    {item}
                  </span>
                ))}
            </div>
          )}

          <div className='project-card__actions'>
            {project.livePreview && (
              <a
                href={project.livePreview}
                className='btn btn--outline'
              >
                Ver detalles
              </a>
            )}
            
            {project.sourceCode && (
              <a
                href={project.sourceCode}
                target='_blank'
                rel='noreferrer'
                className='btn btn--plain'
              >
                Código
              </a>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Projects