import { useContext, useState, useEffect } from 'react'
import Brightness2Icon from '@material-ui/icons/Brightness2'
import WbSunnyRoundedIcon from '@material-ui/icons/WbSunnyRounded'
import MenuIcon from '@material-ui/icons/Menu'
import CloseIcon from '@material-ui/icons/Close'
import { ThemeContext } from '../../contexts/theme'
import { servicios, projects, skills, contact } from '../../portfolio'
import './Navbar.css'

const Navbar = ({ onMenuToggle }) => {
  const [{ themeName, toggleTheme }] = useContext(ThemeContext)
  const [showNavList, setShowNavList] = useState(false)

  // Función para verificar si estamos en vista móvil
  const isMobileView = () => {
    return window.innerWidth <= 600 // Ajusta este valor según tu breakpoint CSS
  }

  const toggleNavList = () => {
    const newShowNavList = !showNavList
    setShowNavList(newShowNavList)
    
    // Comunicar al Header el estado del menú
    if (onMenuToggle) {
      onMenuToggle(newShowNavList)
    }

    // Solo bloquear/desbloquear scroll del body en vista móvil
    if (isMobileView()) {
      if (newShowNavList) {
        document.body.style.overflow = 'hidden'
      } else {
        document.body.style.overflow = 'unset'
      }
    }
  }

  // Limpiar el overflow cuando el componente se desmonte
  useEffect(() => {
    return () => {
      document.body.style.overflow = 'unset'
    }
  }, [])

  // Manejar cambios de tamaño de ventana
  useEffect(() => {
    const handleResize = () => {
      // Si cambiamos a vista desktop y el menú está abierto, restaurar scroll
      if (!isMobileView() && showNavList) {
        document.body.style.overflow = 'unset'
      }
      // Si cambiamos a vista móvil y el menú está abierto, bloquear scroll
      else if (isMobileView() && showNavList) {
        document.body.style.overflow = 'hidden'
      }
    }

    window.addEventListener('resize', handleResize)
    
    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [showNavList])

  // También comunicar el estado cuando el componente se monte/desmonte
  useEffect(() => {
    if (onMenuToggle) {
      onMenuToggle(showNavList)
    }
  }, [showNavList, onMenuToggle])

  return (
    <nav className='center nav'>
      <ul
        style={{ display: showNavList ? 'flex' : null }}
        className='nav__list'
      >

        {servicios.length ? (
          <li className='nav__list-item'>
            <a
              href='#/#servicios'
              onClick={toggleNavList}
              className='link link--nav' 
            >
              Servicios
            </a>
          </li>
        ) : null}


        {projects.length ? (
          <li className='nav__list-item'>
            <a
              href='#/#proyectos'
              onClick={toggleNavList}
              className='link link--nav' 
            >
              Proyectos
            </a>
          </li>
        ) : null}

        {skills.length ? (
          <li className='nav__list-item'>
            <a
              href='#/#skills'
              onClick={toggleNavList}
              className='link link--nav'
            >
              habilidades
            </a>
          </li>
        ) : null}

        {contact.email ? (
          <li className='nav__list-item'>
            <a
              href='#/#contacto'
              onClick={toggleNavList}
              className='link link--nav'
            >
              Contacto
            </a>
          </li>
        ) : null}
      </ul>

      <button
        type='button'
        onClick={toggleTheme}
        className='btn btn--icon nav__theme'
        aria-label='toggle theme'
      >
        {themeName === 'dark' ? <WbSunnyRoundedIcon /> : <Brightness2Icon />}
      </button>

      <button
        type='button'
        onClick={toggleNavList}
        className='nav__hamburger'
        aria-label='toggle navigation'
      >
        {showNavList ? <CloseIcon /> : <MenuIcon />}
      </button>
    </nav>
  )
}

export default Navbar